.trigger-button {
  background-color: #bf2b2b;
  color: #ffffff;
  font-size: 20px;
  border-radius: 40px;
  border-width: 0px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 290px;
  cursor: pointer;
}
.react-icon1 {
  font-size: 50px;
  margin-right: 10px;
}
.login-bg {
  background-image: url(https://resources.commlabindia.com/hubfs/webinars/corporate-training-with-ai-texture-bg.jpg) !important;
  background-size: cover !important;
  background-position: center center !important;
  background-repeat: no-repeat !important;
  width: 600px;
  padding: 10px;
}
.popup-heading {
  color: #263342;
}
.close-button {
  background-color: transparent;
  border-width: 0px;
}
.label {
  color: #263342;
  font-weight: 400;
  font-size: 18px;
}
.for-span {
  color: #bf2b2b;
}
.input {
  display: inline-block;
  width: 100%;
  height: 40px;
  padding: 9px 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #33475b;
  background-color: #f5f8fa;
  border: 1px solid #cbd6e2;
  box-sizing: border-box;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  outline: none;
  margin-right: 20px;
  margin-bottom: 24px;
}
.input2 {
  display: inline-block;
  width: 100%;
  height: 40px;
  padding: 9px 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #33475b;
  background-color: #f5f8fa;
  border: 1px solid #cbd6e2;
  box-sizing: border-box;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  outline: none;
  margin-right: 20px;
}
.input-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.secure-your-seat-button {
  border: 3px solid #ff6633;
  padding-top: 12px;
  padding-right: 30px;
  padding-bottom: 12px;
  padding-left: 30px;
  font-family: "Lexend Deca", sans-serif;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  background-color: rgba(255, 102, 51, 1);
  border-radius: 6px;
  color: #ffffff;
  fill: #ffffff;
  font-size: 18px;
  text-transform: none;
  margin-bottom: 20px;
}
.error {
  font-size: 14px;
  color: #212d3a;
  display: block;
  float: none;
  width: auto;
  font-weight: 500;
  line-height: 20px;
  padding-top: 0;
  color: #e51520;
}
